import React from 'react'

let Todo = (props) => {
    return (
        <div className="todo-item" >
            <input type="checkbox" onChange={(e) => props.onChecked(e.target.checked, props.todoList.id)} />
            <strong className={props.clsName}> {props.todoList.title} </strong>
            <a className="delete-button" onClick={(e) => props.onDelete(props.todoList.id)}>X</a>
        </div>
    )
}
export default Todo


