import React from 'react'

let TodoInput = (props) => {
    return (
        <input
            onKeyPress={(event) => {
                if (event.key === "Enter") {
                    if (event.target.value !== "" && event.target.value.trim()) {
                        props.todoInputValue(event.target.value);
                        event.target.value = "";
                    } else {
                        alert("enter todo here....");
                    }
                }
            }}
            placeholder="Enter todo here.." type="text" />
    )
}
export default TodoInput


