import React, { Component } from 'react';
import './App.css';
import TodoInput from "./components/TodoInput"
import TodosList from "./components/TodosList"
import uuid from "uuid";

class App extends Component {
  constructor() {
    super();
    this.state = {
      allTodos: []
    }
  }

  StoreTodo = (inputVal) => {
    let newTodo = {
      id: uuid.v4(),
      title: inputVal,
      chk: false
    }
    this.setState({
			allTodos: [...this.state.allTodos, newTodo]
		})
  }
  handleCheckTodo = (checkVal,id) =>{
    console.log("Checked");
    console.log(checkVal,id)
  }
  handleDeleteTodo = (id) => {
    console.log("delete")
    console.log(id)
  }

  render() {
    return (
      <div className="App">
        <TodoInput todoInputValue={this.StoreTodo} />
        <TodosList todoList = {this.state.allTodos} onChecked={this.handleCheckTodo}
                    onDelete={this.handleDeleteTodo}/>
      </div>
    );
  }
}
export default App;



