import React from 'react'
import Todo from "./Todo"

let TodoList = (props) => {
    let todoItems;
    if(props.todoList)
    todoItems = props.todoList.map(todo => {
        let clsName = todo.chk ? "checked-todo" : "";
        return (
            <Todo key={todo.id} cls ={clsName} todoList = {todo} onDelete={props.onDelete} onChecked={props.onChecked}/>
        )
    });
    return (
        <div className="todos-list">
            {todoItems}
        </div>
    )
}
export default TodoList


